
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mapMutations} from 'vuex';
export default {
name:'login',
components:{
Group,
XInput,
XButton,
Toast
},
data: function(){
return {
accesstoken:'',
isShowToast:false,
toastTest:''
};
},
methods:{
...mapMutations(['mutationLogin',
'mutationLoginname',
'mutationAccessToken',
'mutationAuthorId']),

dologin:function(){
var app=this;

this.$http.post('/accesstoken',{
accesstoken:this.accesstoken}).then(function(response){




if(response && response.data && response.data.success){

app.mutationLogin();
app.mutationLoginname(response.data.loginname);
app.mutationAccessToken(app.accesstoken);
app.$router.push('/user/'+response.data.loginname);
app.mutationAuthorId(response.data.id);

window.localStorage.setItem('loginStatus',true);
window.localStorage.setItem('loginname',response.data.loginname);
window.localStorage.setItem('accesstoken',app.accesstoken);
window.localStorage.setItem('author_id',response.data.id);
}
}).catch(function(error){


app.isShowToast =true;
app.toastTest =error.response.data.error_msg;
})
}
}
}



