
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name:'user',
props:['loginname'],
data: function(){
return{
userinfo:null,
collect_topics:[]
};
},
components:{
Card,
Group,
Cell
},
beforeRouteEnter: function(to,from,next){
next(function(vm){


vm.$http.get('/user/' +
to.params.loginname).then(function(response){

vm.userinfo = response.data.data;
});

vm.$http.get('/topic_collect/'+to.params.loginname).then(function(response){
vm.collect_topics= response.data.data;
})
});
},
beforeRouteUpdate: function(to,from,next){
var app= this;
this.$http.get('/user/' +
to.params.loginname).then(function(response){

app.userinfo= response.data.data;
});

app.$http.get('/topic_collect/'+to.params.loginname).then(function(response){
vm.collect_topics= response.data.data;
})
next();
}
}




